/* ProductDetailsModal.css */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  max-width: 600px;
  width: 100%;
  height: 500px;
  overflow-y: auto;
  position: relative;
}

.img-main {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-image {
  width: 200px;
  height: auto;
  object-fit: cover;
  display: flex;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  background: none;
  border: none;
  cursor: pointer;
}
